// src/pages/CompanyDetail.jsx
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { Container, Box, Typography, Card, CardMedia } from '@mui/material';
import NavigationBar from '../components/NavigationBar';
import CompanyChart from '../components/CompanyChart';
import companies from '../data/companies.json';

const CompanyDetail = () => {
  const { companyId } = useParams();
  const [chartData, setChartData] = useState(null);

  // companies.json 
  const company = companies.find((c) => String(c.id) === companyId);

  useEffect(() => {
    if (company) {
      // 
      const dummyData = {
        labels: ['2024-01', '2024-02', '2024-03', '2024-04', '2024-05', '2024-06'],
        datasets: [
          {
            label: '',
            data: [company.initialEmployees || 100, 110, 105, 115, 120, 125],
            borderColor: 'rgba(75,192,192,1)',
            backgroundColor: 'rgba(75,192,192,0.2)',
          },
        ],
      };
      setChartData(dummyData);
    }
  }, [company]);

  if (!company) {
    return (
      <Container>
        <NavigationBar />
        <Box sx={{ py: 4 }}>
          <Typography variant="h6" color="error">
            
          </Typography>
        </Box>
      </Container>
    );
  }

  return (
    <Container>
      <NavigationBar />
      <Box sx={{ py: 4 }}>
        <Typography variant="h4" gutterBottom>
          {company.name}
        </Typography>
        <Card sx={{ maxWidth: 200, mb: 4 }}>
          <CardMedia
            component="img"
            image={company.logo} // assets/company-logos/
            alt={`${company.name}`}
          />
        </Card>
        {chartData ? (
          <CompanyChart data={chartData} />
        ) : (
          <Typography variant="body1">...</Typography>
        )}
      </Box>
    </Container>
  );
};

export default CompanyDetail;
